@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);

* {
  margin:0;
  padding: 0;
  font-family: 'Lato';
  /* background-color: #ebe8e8; */
  box-sizing: border-box;
}

a:visited {
  text-decoration: none;
}

header {
  position: relative;
  z-index: 10;
  padding: 10px 20px;
  text-align: center;
  background-color: #ffffff;;
  box-shadow: 5px 0 10px rgb(0 0 0 / 5%);
}
header * {
  background-color: #ffffff;
}

.mainHeader {
  margin: 10px auto;
  font-weight: 700;
}

.rLink,
.rLink:link,
.artistLink:link,
.artistLink,
.songLink {
  text-decoration: none;
  color: black;
}

#searchInput {
  border: 1px solid #bcbec2;
  height: 40px;
  max-width: 484px;
  width: auto;
  padding: 0 10px;
  border-radius: 14px;
  font-size: 16px;
  outline: none;
}

.mainContentPage {
  text-align: center;
}

.trackCtn {
  max-width: 425px;
  margin: 10px auto;
  padding: 10px;
  text-align: left;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 0px 1px .5px rgba(0,0,0,.3);
}

.trackCtn * {
  background-color: #ffffff;
}

.trackContent {
  padding: 10px 5px; 
  margin-bottom: 10px;
  border-bottom: 1px solid #f7f7f7;
}

.trackContent * {
  display: inline-block;
  padding: 0 5px;
}

.trackName p {
  display: block;
  text-align: left;
}

#playButton,
#saveButton {
  height: 28px;
  width: 28px;
}

#playButton:hover {
  cursor: pointer;
}

.playlist-item-ctn {
  display: flex;
  list-style-type: none;
  align-items: center;
  border-bottom: .5px solid #fff;
}

.playlist-item-ctn * {
  
  text-align: center;
  
}

.playlist-item-ctn img {
  width: 28px;
  height: auto;
}

.playlist-item-ctn img:hover {
  cursor: pointer;
}

#more-icon img {
  width: 22px;
  height: auto;
}

.track-name,
.track-artist {
  width: 35%;
}

.play-ctn,
#more-icon {
  width: 15%;
}

.videoplayer {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  
  min-width: 80px;
  z-index: 2;
  border: .4px solid rgb(230, 227, 227);
  bottom: -10px;
  box-shadow: 5px 8px 16px 5px rgba(0,0,0,0.2);
  left: 30px;
  top: 14px;
  
}

.dropdown-content ul {
  list-style-type: none;
  width: 80px;
  font-size: 10px;
  text-align: left;
  
}

.dropdown-content li {
  text-align: left;
  width: 80px;
  padding: 10px 5px;
  background-color: #ffffff;
}

.dropdown-content li:hover {
  background-color: rgb(211, 199, 182);
}

.dropdown:hover .dropdown-content {
  display: block;
}

.remove-btn:hover {
  cursor: pointer;
}

.user-Page ul {
  list-style-type: none;

}

.sim-artist-ctn,
.sim-songs-ctn {
  max-width: 225px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0px 0px 1px .5px rgba(0,0,0,.3);
}

@media only screen and (min-width: 600px) {
  .user-Page {
    margin: 0 auto;
    width: 600px;
  }
}
